.App {
    font-family: sans-serif;
    text-align: center;
}
nav {
  background-color: rgba(27, 44, 85, 0.95);
  backdrop-filter: blur(10px);
}
.nav-item,
.dropdown,
.dropdown-item {
  transition: 230ms ease-in-out;
}
.nav-item.active,
.dropdown-toggle.active,
.dropdown-item.active {
  background-color: lightblue;
}
.nav-item.active a,
.dropdown-toggle.active a,
.dropdown-item.active a {
  color: black !important;
}
.dropdown {
  vertical-align: middle;
}
.dropdown-toggle {
  display: inline-block;
  vertical-align: middle;
}
.vh-60 {
  height: 60vh;
}
.min-vh-60 {
  min-height: 60vh;
}
.row {
  width: 100%;
  margin: 0;
}
.imptSection {
  border: 5px solid white;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  text-align: center;
}
.fitImg {
  width: 80%;
  height: auto;
}
.fitSmallImg {
  width: 70% !important;
  height: auto;
}
.font-20 {
  font-size: 20px;
}
.font-18 {
  font-size: 18px;
}
.font-17 {
  font-size: 17px;
}
.font-16 {
  font-size: 16px;
}
.py-7 {
  padding-top: 7vmax;
  padding-bottom: 7vmax;
}
.scroll-down {
  font-size: 30px;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.7)
}

html {
  scroll-behavior: smooth;
}
.sectionHeader {
  border-bottom: 1px solid black;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
}

#DarkModeTogglerDiv {
  right: 0;
}

.dark {
  transition: 180ms ease-in-out;
}
@media (prefers-color-scheme: dark) {
  .PageTemplateClass {
      background-color: black ;
  }
    .PageTemplateClass p, 
    .PageTemplateClass span, 
    .PageTemplateClass h1, 
    .PageTemplateClass h2,
    .PageTemplateClass h3,
    .PageTemplateClass h4,
    .PageTemplateClass h5,
    .PageTemplateClass h6, 
    .PageTemplateClass .fas {
      color: white;
    }
    .bg-on-dark-positive {
      background-color: rgba(0, 0, 0);
    }
    .bg-on-dark-negative {
      background-color: rgba(255, 255, 255);
    }
    .col-on-dark-positive {
      color: rgba(0, 0, 0);
    }
    .col-on-dark-negative {
      color: rgba(255, 255, 255);
    }
    .bg-on-dark-positive-trans {
      background-color: rgba(0, 0, 0, 0.7)
    }
    .PageTemplateClass a {
      color: #007bff;
    }
    .PageTemplateClass a:hover {
      color: #4da3ff;
    }
    .dark .scroll-down {
      background-color: rgba(0, 0, 0, 0.7);
      color: white;
    }
}